/**
 * 
 * @param {string} path - 
 * @returns {{ valid: boolean, error?: string }} 
 */
export function validateFolderPath(path) {
    // 
    if (!path || path.trim() === '') {
        return { valid: true };
    }
    
    //  "/"
    if (path === '/') {
        return { valid: true };
    }
    
    //  "/" 
    if (!path.startsWith('/')) {
        return { 
            valid: false, 
            error: ' "/" ' 
        };
    }
    
    // 
    // : \ : * ? " ' < > |  ( ) [ ] { } # % ^ ` ~ ; @ & = + $ ,
    const invalidChars = /[\\:\*\?"'<>\| \(\)\[\]\{\}#%\^`~;@&=\+\$,]/;
    if (invalidChars.test(path)) {
        return { 
            valid: false, 
            error: '' 
        };
    }
    
    // 
    if (path.includes('//')) {
        return { 
            valid: false, 
            error: '' 
        };
    }
    
    return { valid: true };
}
