<template>
    <div class="upload-form">
        <div class="upload-card-wrapper" @mousemove="handleUploadCardMouseMove" @mouseleave="handleUploadCardMouseLeave">
            <div class="upload-card-glow" ref="uploadCardGlow"></div>
            
            <!-- Default File Upload Mode -->
            <v-card
                v-if="uploadMethod === 'default'"
                class="upload-card d-flex flex-column align-center justify-center"
                :class="{'is-uploading': uploading, 'upload-card-busy': fileList.length}"
                elevation="10"
                rounded="xl"
                @dragover.prevent="handleDragOver"
                @dragleave.prevent="handleDragLeave"
                @drop.prevent="handleDrop"
                @click="triggerFileInput"
            >
                <div class="d-flex flex-column align-center justify-center upload-drop-zone" :class="{'is-dragover': isDragOver}">
                     <v-icon 
                        :size="fileList.length ? 60 : 100" 
                        color="primary" 
                        class="mb-4 upload-icon"
                        :class="{'upload-list-busy': fileList.length}"
                    >
                        mdi-cloud-upload
                    </v-icon>
                    <div class="text-medium-emphasis text-center upload-text" :class="{'upload-list-busy': fileList.length}">
                        <em></em> <em></em>  <em>Ctrl + V</em> 
                    </div>
                    <input 
                        type="file" 
                        ref="fileInput" 
                        class="d-none" 
                        multiple 
                        @change="handleFileSelect" 
                        @click.stop
                    />
                </div>
            </v-card>

            <!-- Paste Upload Mode -->
            <v-card 
                v-if="uploadMethod === 'paste'" 
                class="paste-card"
                :class="{'is-uploading': uploading, 'upload-card-busy': fileList.length}"
                elevation="10"
                rounded="xl"
            >
                <v-textarea
                    v-model="pastedUrls"
                    class="upload-card-textarea"
                    placeholder=""
                    :rows="fileList.length ? 4 : 14"
                    variant="solo-filled"
                    bg-color="surface"
                    hide-details
                    no-resize
                ></v-textarea>
                
                <div class="paste-card-actions mt-4 d-flex align-center justify-space-between w-100 px-4">
                    <v-btn
                        color="primary"
                        :size="pasteCardMethodButtonSize"
                        @click="handleUploadPasteUrls"
                        class="paste-card-upload-button font-weight-bold"
                        elevation="4"
                    >
                         
                    </v-btn>
                    
                     <v-btn-toggle
                        v-model="pasteUploadMethod"
                        mandatory
                        rounded="xl"
                        color="primary"
                        class="paste-card-method-group"
                        density="compact"
                    >
                        <v-btn value="save" size="small"></v-btn>
                        <v-btn value="external" size="small"></v-btn>
                    </v-btn-toggle>
                </div>
            </v-card>
        </div>

        <!-- File List & Dashboard -->
        <v-card 
            class="upload-list-card mt-4 overflow-hidden" 
            :class="{'upload-list-busy': fileList.length, 'is-uploading': uploading}"
            elevation="10"
            rounded="xl"
        >
            <div class="upload-list-container d-flex flex-column" :class="{'upload-list-busy': fileList.length}">
                 <!-- Dashboard Header -->
                <div class="upload-list-dashboard d-flex align-center justify-space-between px-4 py-2" :class="{ 'list-scrolled': listScrolled }">
                    <div class="d-flex align-center cursor-pointer status-toggle" @click="toggleStatusView">
                        <v-chip size="small" color="primary" class="mr-2" variant="elevated">
                            <v-icon start icon="mdi-format-list-bulleted"></v-icon>
                            {{ uploadingCount + waitingCount }}
                        </v-chip>
                        <v-chip size="small" color="success" class="mr-2" variant="elevated">
                             <v-icon start icon="mdi-check-circle"></v-icon>
                            {{ uploadSuccessCount }}
                        </v-chip>
                         <v-chip size="small" color="error" variant="elevated">
                             <v-icon start icon="mdi-alert-circle"></v-icon>
                            {{ uploadErrorCount }}
                        </v-chip>
                    </div>

                    <div class="modern-action-group d-flex align-center">
                        <v-tooltip location="top" text="">
                            <template v-slot:activator="{ props }">
                                <v-btn icon="mdi-content-copy" variant="text" size="small" color="primary" v-bind="props" @click="copyAll"></v-btn>
                            </template>
                        </v-tooltip>
                        
                         <v-menu>
                            <template v-slot:activator="{ props }">
                                <v-btn icon="mdi-reload" variant="text" size="small" color="primary" v-bind="props" @click="retryError">
                                    <v-tooltip activator="parent" location="top"></v-tooltip>
                                </v-btn>
                            </template>
                            <v-list density="compact">
                                <v-list-item>
                                    <div class="d-flex align-center">
                                        <span class="text-caption mr-2"></span>
                                        <v-switch v-model="autoReUpload" @change="handleAutoRetryChange" hide-details density="compact" color="primary"></v-switch>
                                    </div>
                                </v-list-item>
                            </v-list>
                        </v-menu>

                        <v-menu>
                             <template v-slot:activator="{ props }">
                                <v-btn icon="mdi-delete" variant="text" size="small" color="error" v-bind="props">
                                     <v-tooltip activator="parent" location="top"></v-tooltip>
                                </v-btn>
                            </template>
                            <v-list density="compact">
                                <v-list-item @click="clearFileList" title=""></v-list-item>
                                <v-list-item @click="clearSuccessList" title=""></v-list-item>
                            </v-list>
                        </v-menu>
                    </div>
                </div>

                <!-- Scrollable List -->
                <div class="flex-grow-1 overflow-y-auto" ref="scrollContainer" @scroll="handleScroll">
                    <v-list bg-color="transparent" class="pa-0">
                        <v-list-item
                            v-for="file in displayedFileList"
                            :key="file.uid"
                            class="mb-2 px-4"
                        >
                            <template v-slot:prepend>
                                 <v-avatar rounded="lg" size="60" class="mr-4">
                                     <v-img
                                        v-if="isImage(file.name)"
                                        :src="file.url"
                                        cover
                                        @error="file.url = require('@/assets/404.png')"
                                    ></v-img>
                                    <video
                                        v-else-if="isVideo(file.name)"
                                        :src="file.url"
                                        style="width: 100%; height: 100%; object-fit: cover;"
                                        autoplay muted playsinline loop
                                    ></video>
                                    <v-icon v-else icon="mdi-file" size="large" color="medium-emphasis"></v-icon>
                                </v-avatar>
                            </template>

                            <v-list-item-title class="font-weight-medium mb-1">
                                {{ truncateFilename(file.name) }}
                            </v-list-item-title>

                            <div v-if="file.status === 'done' || file.status === 'success'" class="d-flex flex-column gap-2" style="width: 100%; max-width: 600px;">
                                <div class="d-flex gap-2 mb-1">
                                    <v-text-field
                                        v-model="file.finalURL"
                                        readonly
                                        variant="outlined"
                                        density="compact"
                                        hide-details
                                        bg-color="surface"
                                        @click="selectAllText"
                                        label="URL"
                                        class="flex-grow-1"
                                    ></v-text-field>
                                     <v-text-field
                                        v-model="file.mdURL"
                                        readonly
                                        variant="outlined"
                                        density="compact"
                                        hide-details
                                        bg-color="surface"
                                        @click="selectAllText"
                                        label="Markdown"
                                        class="flex-grow-1"
                                    ></v-text-field>
                                </div>
                                <div class="d-flex gap-2">
                                     <v-text-field
                                        v-model="file.htmlURL"
                                        readonly
                                        variant="outlined"
                                        density="compact"
                                        hide-details
                                        bg-color="surface"
                                        @click="selectAllText"
                                        label="HTML"
                                         class="flex-grow-1"
                                    ></v-text-field>
                                     <v-text-field
                                        v-model="file.ubbURL"
                                        readonly
                                        variant="outlined"
                                        density="compact"
                                        hide-details
                                        bg-color="surface"
                                        @click="selectAllText"
                                        label="BBCode"
                                         class="flex-grow-1"
                                    ></v-text-field>
                                </div>
                            </div>
                             <div v-else class="w-100 pr-4">
                                <v-progress-linear
                                    :model-value="file.progreess"
                                    :color="file.status === 'exception' ? 'error' : 'primary'"
                                    height="8"
                                    rounded
                                    striped
                                ></v-progress-linear>
                                <div class="text-caption text-right mt-1">{{ file.progreess }}%</div>
                            </div>

                            <template v-slot:append>
                                <div class="d-flex flex-column gap-2 ml-2">
                                     <v-btn
                                        icon="mdi-link"
                                        variant="tonal"
                                        size="small"
                                        color="primary"
                                        @click="handleCopy(file)"
                                        :disabled="file.status !== 'done' && file.status !== 'success'"
                                    ></v-btn>
                                     <v-btn
                                        icon="mdi-delete"
                                        variant="tonal"
                                        size="small"
                                        color="error"
                                        @click="handleRemove(file)"
                                    ></v-btn>
                                </div>
                            </template>
                        </v-list-item>
                    </v-list>
                </div>
            </div>
        </v-card>
    </div>
</template>

<script>
import axios from '@/utils/axios'
import * as imageConversion from 'image-conversion'
import { mapGetters } from 'vuex'

export default {
    name: 'UploadForm',
    props: {
        selectedUrlForm: { type: String, default: 'url' },
        customerCompress: { type: Boolean, default: true },
        compressQuality: { type: Number, default: 4 },
        compressBar: { type: Number, default: 5 },
        serverCompress: { type: Boolean, default: true },
        uploadChannel: { type: String, default: 'telegram' },
        channelName: { type: String, default: '' },
        uploadNameType: { type: String, default: 'default' },
        useCustomUrl: { type: String, default: 'false' },
        customUrlPrefix: { type: String, default: '' },
        autoRetry: { type: Boolean, default: true },
        urlPrefix: { type: String, default: '' },
        uploadMethod: { type: String, default: 'default' },
        uploadFolder: { type: String, default: '' },
        convertToWebp: { type: Boolean, default: false }
    },
    data() {
        return {
            fileList: [],
            uploading: false,
            maxUploading: 6,
            exceptionList: [],
            listScrolled: false,
            uploadCount: 0,
            pastedUrls: '',
            pasteUploadMethod: 'save',
            autoReUpload: true,
            maxRetryCount: 10,
            retryTimer: null,
            retryDelay: 12000,
            uploadQueue: [],
            activeUploads: 0,
            maxConcurrentUploads: 6,
            abortControllers: new Map(),
            isDragOver: false,
        }
    },
    computed: {
        ...mapGetters(['storeAutoReUpload']),
        uploadSuccessCount() { return this.fileList.filter(item => item.status === 'done' || item.status === 'success').length },
        uploadErrorCount() { return this.fileList.filter(item => item.status === 'exception').length },
        uploadingCount() { return this.fileList.filter(item => item.status === 'uploading').length },
        waitingCount() { return this.uploadQueue.length },
        urlSize() { return window.innerWidth < 768 ? 'compact' : 'default' },
        rootUrl() { return this.useCustomUrl === 'true' ? this.customUrlPrefix : this.urlPrefix || `${window.location.protocol}//${window.location.host}/file/` },
        pasteCardMethodButtonSize() { return this.fileList.length ? 'small' : (window.innerWidth < 768 ? 'small' : 'large') },
        displayedFileList() { return this.fileList.slice().reverse() }
    },
    watch: {
        fileList: {
            handler() {
                if (this.fileList.length > 0) {
                     this.$nextTick(() => {
                        // Scroll logic if needed
                    })
                }
            },
            deep: true
        },
        useCustomUrl: {
            handler() { this.updateFileUrls() },
            immediate: true
        },
        customUrlPrefix: {
            handler() { this.updateFileUrls() },
            immediate: true
        },
        autoReUpload(val) { this.$store.commit('setStoreAutoReUpload', val) }
    },
    mounted() {
        document.addEventListener('paste', this.handlePaste)
        this.autoReUpload = this.storeAutoReUpload
    },
    beforeUnmount() {
        document.removeEventListener('paste', this.handlePaste)
        this.uploadQueue = []
        this.fileList = []
        this.activeUploads = 0
        if (this.retryTimer) clearTimeout(this.retryTimer)
    },
    methods: {
        updateFileUrls() {
            this.fileList.forEach(item => {
                if (item.uploadChannel === 'external') return
                const prefix = this.useCustomUrl === 'true' ? this.customUrlPrefix : this.rootUrl
                if (!item.srcID) return
                item.finalURL = prefix + item.srcID
                item.mdURL = `![${item.name}](${prefix + item.srcID})`
                item.htmlURL = `<img src="${prefix + item.srcID}" alt="${item.name}" width=100% />`
                item.ubbURL = `[img]${prefix + item.srcID}[/img]`
            })
        },
        truncateFilename(filename, maxLength = 20) {
            if (!filename || filename.length <= maxLength) return filename
            const lastDotIndex = filename.lastIndexOf('.')
            const name = lastDotIndex > 0 ? filename.substring(0, lastDotIndex) : filename
            const ext = lastDotIndex > 0 ? filename.substring(lastDotIndex) : ''
            const keepEnd = ext.length + 4
            const keepStart = maxLength - keepEnd - 3
            if (keepStart <= 0) return filename.substring(0, maxLength - 3) + '...'
            return name.substring(0, keepStart) + '...' + name.slice(-4) + ext
        },
        triggerFileInput() { this.$refs.fileInput.click() },
        handleFileSelect(e) {
            const files = Array.from(e.target.files)
            files.forEach(file => {
                file.uid = Date.now() + Math.random()
                this.beforeUpload(file).then(processedFile => {
                   this.uploadFile({ file: processedFile, onSuccess: this.handleSuccess, onError: this.handleError, onProgress: this.handleProgress })
                }).catch(err => console.error(err))
            })
            e.target.value = '' // Reset input
        },
        handleDragOver(e) { this.isDragOver = true },
        handleDragLeave(e) { this.isDragOver = false },
        handleDrop(e) {
            this.isDragOver = false
            const files = Array.from(e.dataTransfer.files)
            files.forEach(file => {
                file.uid = Date.now() + Math.random()
                this.beforeUpload(file).then(processedFile => {
                   this.uploadFile({ file: processedFile, onSuccess: this.handleSuccess, onError: this.handleError, onProgress: this.handleProgress })
                }).catch(err => console.error(err))
            })
        },
        // Core Upload Logic (Adapted from original)
        uploadFile(options) {
            const { file } = options
            // Find in file list
            if (!this.fileList.find(item => item.uid === file.uid)) return

            if (this.activeUploads >= this.maxConcurrentUploads) {
                this.uploadQueue.push(options)
                const waitingItem = this.fileList.find(item => item.uid === file.uid)
                if (waitingItem) waitingItem.status = 'waiting'
                return
            }

            this.activeUploads++
            const uploadingItem = this.fileList.find(item => item.uid === file.uid)
            if (uploadingItem) uploadingItem.status = 'uploading'
             
            const fileItem = this.fileList.find(item => item.uid === file.uid)
            const uploadChannel = fileItem?.uploadChannel || this.uploadChannel

            if (uploadChannel === 'external') {
                this.uploadSingleFile(options)
                return
            }

            const HF_DIRECT_THRESHOLD = 20 * 1024 * 1024
            if (uploadChannel === 'huggingface' && file.size >= HF_DIRECT_THRESHOLD) {
                this.uploadToHuggingFaceDirect(options)
                return
            }
            
            const DISCORD_CHUNK_THRESHOLD = 9 * 1024 * 1024
            if (uploadChannel === 'discord') {
                if (file.size > DISCORD_CHUNK_THRESHOLD) this.uploadFileInChunks(options)
                else this.uploadSingleFile(options)
                return
            }
            
             const CHUNK_THRESHOLD = 20 * 1024 * 1024
             if (file.size > CHUNK_THRESHOLD) this.uploadFileInChunks(options)
             else this.uploadSingleFile(options)
        },
        processUploadQueue() {
            if (this.uploadQueue.length === 0 || this.activeUploads >= this.maxConcurrentUploads) return
            const nextOptions = this.uploadQueue.shift()
            if (nextOptions && this.fileList.find(item => item.uid === nextOptions.file.uid)) {
                this.uploadFile(nextOptions)
            } else {
                this.processUploadQueue()
            }
        },
        onUploadComplete() {
            this.activeUploads = Math.max(0, this.activeUploads - 1)
            this.processUploadQueue()
             if (this.activeUploads === 0 && this.uploadQueue.length === 0) {
                this.uploading = false
            }
        },
         async uploadSingleFile(options) {
             const { file } = options
             const fileItem = this.fileList.find(item => item.uid === file.uid)
             if (!fileItem) return
             
            const needServerCompress = fileItem.serverCompress
            const uploadChannel = fileItem.uploadChannel || this.uploadChannel
            const autoRetry = this.autoRetry && uploadChannel !== 'external'
            const uploadNameType = uploadChannel === 'external' ? 'default' : this.uploadNameType

            const abortController = new AbortController()
            this.abortControllers.set(file.uid, abortController)

            const formData = new FormData()
            formData.append('file', file)
            if (uploadChannel === 'external') formData.append('url', file.url)

             if (uploadChannel === 'huggingface') {
                try {
                     const sha256 = await this.computeSha256(file)
                     formData.append('sha256', sha256)
                } catch (err) { console.error('Failed to compute SHA256', err) }
             }

             axios({
                 url: '/upload' +
                     `?serverCompress=${needServerCompress}&uploadChannel=${uploadChannel}` +
                     (this.channelName ? `&channelName=${encodeURIComponent(this.channelName)}` : '') +
                     `&uploadNameType=${uploadNameType}&autoRetry=${autoRetry}&uploadFolder=${this.uploadFolder}`,
                 method: 'post',
                 data: formData,
                 withAuthCode: true,
                 signal: abortController.signal,
                 onUploadProgress: (progressEvent) => {
                     const percentCompleted = Math.round((progressEvent.loaded / progressEvent.total) * 100)
                     this.handleProgress({ percent: percentCompleted, file: file })
                 }
             }).then(res => {
                 this.handleSuccess(res, file)
             }).catch(err => {
                 if (err.name === 'CanceledError' || err.code === 'ERR_CANCELED') return
                  if (err.response && err.response.status !== 401) {
                    this.exceptionList.push({ file, retryCount: fileItem.retryCount })
                    this.handleError(err, file)
                } else if (!err.response) {
                     this.exceptionList.push({ file, retryCount: fileItem.retryCount })
                    this.handleError(err, file)
                }
             }).finally(() => {
                 this.abortControllers.delete(file.uid)
                 this.onUploadComplete()
             })
         },

    // ... Helper Methods ...
    handleUploadCardMouseMove(e) {
        const glow = this.$refs.uploadCardGlow;
        if (!glow) return;
        // Check if event has currentTarget
        if (!e.currentTarget) return; 
        const rect = e.currentTarget.getBoundingClientRect();
        const x = e.clientX - rect.left;
        const y = e.clientY - rect.top;
        glow.style.opacity = '1';
        glow.style.left = x + 'px';
        glow.style.top = y + 'px';
    },
    handleUploadCardMouseLeave() {
        const glow = this.$refs.uploadCardGlow;
        if (!glow) return;
        glow.style.opacity = '0';
    },
    saveToHistory(fileItem) {
        try {
            const history = JSON.parse(localStorage.getItem('uploadHistory') || '[]')
            const newRecord = {
                name: fileItem.name,
                url: fileItem.finalURL,
                time: Date.now(),
                type: fileItem.name.split('.').pop().toLowerCase()
            }
            history.push(newRecord)
            localStorage.setItem('uploadHistory', JSON.stringify(history))
        } catch (e) { console.error('Failed to save history', e) }
    },
    copyAll() {
        // Collect URLs based on current format
        const fieldMap = {
            'url': 'finalURL', 'md': 'mdURL', 'html': 'htmlURL', 'ubb': 'ubbURL'
        }
        const field = fieldMap[this.selectedUrlForm] || 'finalURL'
        const urls = this.fileList
            .filter(item => item.status === 'done' || item.status === 'success')
            .map(item => item[field])
            .join('\n')
        
        if (urls) {
            navigator.clipboard.writeText(urls)
            this.$message.success('')
        } else {
            this.$message.info('')
        }
    },
    clearFileList() {
        if (this.fileList.length > 0) {
            this.abortControllers.forEach(c => c.abort())
            this.abortControllers.clear()
            this.uploadQueue = []
            this.fileList = []
            this.$message.success('')
        }
    },
    clearSuccessList() {
        if (this.uploadSuccessCount > 0) {
            this.fileList = this.fileList.filter(item => item.status !== 'done' && item.status !== 'success')
            this.$message.success('')
        }
    },
    handleRemove(file) {
        if (this.abortControllers.has(file.uid)) {
            this.abortControllers.get(file.uid).abort()
            this.abortControllers.delete(file.uid)
        }
        this.uploadQueue = this.uploadQueue.filter(item => item.file.uid !== file.uid)
        this.fileList = this.fileList.filter(item => item.uid !== file.uid)
        this.$message.info(`${this.truncateFilename(file.name)} `)
    },
    handleCopy(file) {
        if (file.status !== 'done' && file.status !== 'success') return
         const fieldMap = {
            'url': 'finalURL', 'md': 'mdURL', 'html': 'htmlURL', 'ubb': 'ubbURL'
        }
        const field = fieldMap[this.selectedUrlForm] || 'finalURL'
        navigator.clipboard.writeText(file[field])
        this.$message.success('')
    },
    selectAllText(event) {
        event.target.select()
        navigator.clipboard.writeText(event.target.value)
            .then(() => this.$message.success(''))
            .catch(() => this.$message.error(''))
    },
    isImage(fileName) {
        const exts = ['jpg', 'jpeg', 'png', 'gif', 'bmp', 'webp', 'svg', 'tiff', 'ico', 'avif', 'heic']
        return exts.includes(fileName.split('.').pop().toLowerCase())
    },
    isVideo(fileName) {
        const exts = ['mp4', 'webm', 'ogg', 'mkv']
        return exts.includes(fileName.split('.').pop().toLowerCase())
    },
    handleScroll(event) {
        // Vuetify list scroll handling if needed
        // this.listScrolled = event.target.scrollTop > 0
    },
    retryError() {
        if (this.exceptionList.length > 0) {
            this.retryFailedFiles(this.exceptionList)
            this.exceptionList = []
        } else {
            this.$message.info('')
        }
    },
    handleAutoRetryChange(val) {
        this.$message({ type: val ? 'success' : 'info', message: val ? '' : '' })
        if (val && this.exceptionList.length > 0) this.scheduleAutoRetry()
    },
    scheduleAutoRetry() {
        if (this.retryTimer) clearTimeout(this.retryTimer)
        this.retryTimer = setTimeout(() => {
            if (this.autoReUpload && this.exceptionList.length > 0) {
                const filesToRetry = [...this.exceptionList]
                this.exceptionList = []
                this.retryFailedFiles(filesToRetry)
            }
        }, this.retryDelay)
    },
    retryFailedFiles(files) {
        files.forEach(item => {
            const retryCount = item.retryCount || 0
            if (retryCount < this.maxRetryCount) {
                // Find file in list, update status
                const fileItem = this.fileList.find(f => f.uid === item.file.uid)
                if(fileItem) {
                    fileItem.retryCount = retryCount + 1
                    fileItem.status = 'uploading' // Reset status
                    this.uploadFile({ 
                        file: item.file, 
                         onProgress: (evt) => this.handleProgress(evt), 
                         onSuccess: (res, f) => this.handleSuccess(res, f), 
                         onError: (err, f) => this.handleError(err, f)
                    })
                }
            } else {
                this.$message.warning(`${item.file.name} `)
            }
        })
    },
    handlePaste(event) {
        if (event.target.tagName === 'INPUT' || event.target.tagName === 'TEXTAREA') return
        const items = event.clipboardData.items
        if (items.length > 0) this.uploadFromUrl(items)
    },
    handleUploadPasteUrls() {
        const urls = this.pastedUrls.split('\n').map(u => u.trim()).filter(u => u !== '' && /^(https?:\/\/[^\s$.?#].[^\s]*)$/.test(u))
        if (this.pasteUploadMethod === 'save') {
             this.uploadFromUrl(urls.map(url => ({ kind: 'string', getAsString: cb => cb(url) })))
        } else {
             urls.forEach((url, i) => {
                 const fileName = url.split('/').pop() || `file_${Date.now()}`
                 const file = new File([], fileName, { type: 'text/plain' })
                 file.uid = Date.now() + i
                 file.url = url // special prop for external
                 
                  this.fileList.push({
                     uid: file.uid, name: file.name, url: url,
                     finalURL: url, mdURL: `![${fileName}](${url})`, htmlURL: `<img src="${url}" alt="${fileName}" width=100% />`, ubbURL: `[img]${url}[/img]`,
                     srcID: url, status: 'uploading', progreess: 0, serverCompress: false, uploadChannel: 'external', retryCount: 0
                 })
                 this.uploadFile({ file, onSuccess: this.handleSuccess, onError: this.handleError, onProgress: this.handleProgress })
             })
        }
    },
    uploadFromUrl(items) {
         for (let i = 0; i < items.length; i++) {
             if (items[i].kind === 'file') {
                 const file = items[i].getAsFile()
                 if(file) {
                    file.uid = Date.now() + i
                    this.beforeUpload(file).then(newFile => {
                         this.uploadFile({ file: newFile, onSuccess: this.handleSuccess, onError: this.handleError, onProgress: this.handleProgress })
                    })
                 }
             } else if (items[i].kind === 'string') {
                 items[i].getAsString(text => {
                     if (/^(https?:\/\/[^\s$.?#].[^\s]*)$/.test(text)) {
                         axios.post('/api/fetchRes', { url: text }, { responseType: 'blob' }).then(res => {
                             if(res.status === 200) {
                                 let fileName = ''
                                 const disp = res.headers['content-disposition']
                                 if(disp) {
                                     const match = /filename[^;=\n]*=((['"]).*?\2|[^;\n]*)/.exec(disp)
                                     if(match && match[1]) fileName = match[1].replace(/['"]/g, '')
                                 }
                                 if(!fileName) fileName = text.split('/').pop() || `pasted_${Date.now()}.bin`
                                 
                                 const blob = res.data
                                 const file = new File([blob], fileName, { type: blob.type })
                                 file.uid = Date.now() + i
                                  this.beforeUpload(file).then(newFile => {
                                     this.uploadFile({ file: newFile, onSuccess: this.handleSuccess, onError: this.handleError, onProgress: this.handleProgress })
                                })
                             }
                         }).catch(() => this.$message.warning('URL'))
                     }
                 })
             }
         }
    },
    async convertImageToWebp(file) {
        return new Promise((resolve, reject) => {
            if (file.type.match(/(gif|svg|webp)/)) { resolve(null); return }
            const img = new Image()
            const canvas = document.createElement('canvas')
            const ctx = canvas.getContext('2d')
            img.onload = () => {
                canvas.width = img.width; canvas.height = img.height
                ctx.drawImage(img, 0, 0)
                canvas.toBlob(blob => {
                    if(blob) {
                         const newName = file.name.replace(/\.[^/.]+$/, "") + ".webp"
                         const webpFile = new File([blob], newName, { type: 'image/webp' })
                         webpFile.uid = file.uid
                         resolve(webpFile)
                    } else reject(new Error('WebP Conversion failed'))
                    URL.revokeObjectURL(img.src)
                }, 'image/webp', 0.92)
            }
            img.onerror = () => { URL.revokeObjectURL(img.src); reject(new Error('Image load failed')) }
            img.src = URL.createObjectURL(file)
        })
    },
    createSha256() {
          // SHA256 implementation (Simplified for brevity, assuming existing logic or library usage)
          // In a real scenario I must provide the FULL SHA256 logic.
          // Since I cannot implement full SHA256 JS here easily without bloating, I will check if I can import it or if I must write it.
          // The previous code had a full JS implementation (~100 lines). I should probably restore it.
          // For now, I'll attempt to use a placeholder or assume the user accepts a partial restore if I can't fit it.
          // BUT the user wants a working app.
          // I'll assume I can copy the implementation I saw efficiently.
          // Due to token limits, I'll paste the core logic I saw in Step 89.
          
          const K = [0x428a2f98, 0x71374491, 0xb5c0fbcf, 0xe9b5dba5, 0x3956c25b, 0x59f111f1, 0x923f82a4, 0xab1c5ed5,0xd807aa98, 0x12835b01, 0x243185be, 0x550c7dc3, 0x72be5d74, 0x80deb1fe, 0x9bdc06a7, 0xc19bf174,0xe49b69c1, 0xefbe4786, 0x0fc19dc6, 0x240ca1cc, 0x2de92c6f, 0x4a7484aa, 0x5cb0a9dc, 0x76f988da,0x983e5152, 0xa831c66d, 0xb00327c8, 0xbf597fc7, 0xc6e00bf3, 0xd5a79147, 0x06ca6351, 0x14292967,0x27b70a85, 0x2e1b2138, 0x4d2c6dfc, 0x53380d13, 0x650a7354, 0x766a0abb, 0x81c2c92e, 0x92722c85,0xa2bfe8a1, 0xa81a664b, 0xc24b8b70, 0xc76c51a3, 0xd192e819, 0xd6990624, 0xf40e3585, 0x106aa070,0x19a4c116, 0x1e376c08, 0x2748774c, 0x34b0bcb5, 0x391c0cb3, 0x4ed8aa4a, 0x5b9cca4f, 0x682e6ff3,0x748f82ee, 0x78a5636f, 0x84c87814, 0x8cc70208, 0x90befffa, 0xa4506ceb, 0xbef9a3f7, 0xc67178f2].map(x=>x>>>0);
          const H_INIT = [0x6a09e667, 0xbb67ae85, 0x3c6ef372, 0xa54ff53a, 0x510e527f, 0x9b05688c, 0x1f83d9ab, 0x5be0cd19].map(x=>x>>>0);
          
          let H = [...H_INIT];
          let buffer = new Uint8Array(64);
          let bufferLength = 0;
          let totalLength = 0;
          
          const rotr = (x, n) => (x >>> n) | (x << (32 - n));
          const processBlock = (block) => {
              const W = new Uint32Array(64);
              for (let i = 0; i < 16; i++) W[i] = (block[i*4]<<24)|(block[i*4+1]<<16)|(block[i*4+2]<<8)|block[i*4+3];
              for (let i = 16; i < 64; i++) {
                  const s0 = rotr(W[i-15],7)^rotr(W[i-15],18)^(W[i-15]>>>3);
                  const s1 = rotr(W[i-2],17)^rotr(W[i-2],19)^(W[i-2]>>>10);
                  W[i] = (W[i-16]+s0+W[i-7]+s1)>>>0;
              }
              let [a,b,c,d,e,f,g,h] = H;
              for (let i = 0; i < 64; i++) {
                  const S1 = rotr(e,6)^rotr(e,11)^rotr(e,25);
                  const ch = (e&f)^((~e)&g);
                  const temp1 = (h+S1+ch+K[i]+W[i])>>>0;
                  const S0 = rotr(a,2)^rotr(a,13)^rotr(a,22);
                  const maj = (a&b)^(a&c)^(b&c);
                  const temp2 = (S0+maj)>>>0;
                  h=g;g=f;f=e;e=(d+temp1)>>>0;d=c;c=b;b=a;a=(temp1+temp2)>>>0;
              }
              H[0]=(H[0]+a)>>>0;H[1]=(H[1]+b)>>>0;H[2]=(H[2]+c)>>>0;H[3]=(H[3]+d)>>>0;
              H[4]=(H[4]+e)>>>0;H[5]=(H[5]+f)>>>0;H[6]=(H[6]+g)>>>0;H[7]=(H[7]+h)>>>0;
          };

          return {
              update(data) {
                  totalLength += data.length;
                  let offset = 0;
                  if (bufferLength > 0) {
                      const needed = 64 - bufferLength;
                      const toCopy = Math.min(needed, data.length);
                      buffer.set(data.subarray(0, toCopy), bufferLength);
                      bufferLength += toCopy;
                      offset = toCopy;
                      if (bufferLength === 64) { processBlock(buffer); bufferLength = 0; }
                  }
                  while (offset + 64 <= data.length) { processBlock(data.subarray(offset, offset + 64)); offset += 64; }
                  if (offset < data.length) { buffer.set(data.subarray(offset), 0); bufferLength = data.length - offset; }
              },
              digest() {
                  const bitLength = totalLength * 8;
                  buffer[bufferLength++] = 0x80;
                  if (bufferLength > 56) { buffer.fill(0, bufferLength, 64); processBlock(buffer); bufferLength = 0; }
                  buffer.fill(0, bufferLength, 56);
                  const view = new DataView(buffer.buffer);
                  view.setUint32(56, Math.floor(bitLength / 0x100000000), false);
                  view.setUint32(60, bitLength>>>0, false);
                  processBlock(buffer);
                  return Array.from(H).map(h=>h.toString(16).padStart(8,'0')).join('');
              }
          }
    },
    async computeSha256(file) {
        const sha256 = this.createSha256()
        const CHUNK_SIZE = 4 * 1024 * 1024
        let offset = 0
        while (offset < file.size) {
            const chunk = file.slice(offset, Math.min(offset + CHUNK_SIZE, file.size))
            const buffer = await chunk.arrayBuffer()
            sha256.update(new Uint8Array(buffer))
            offset += CHUNK_SIZE
        }
        return sha256.digest()
    },
    async uploadFileInChunks(options) {
        const { file } = options
        const fileItem = this.fileList.find(item => item.uid === file.uid)
        if (!fileItem) return
        
        const uploadChannel = fileItem.uploadChannel || this.uploadChannel
        const abortController = new AbortController()
        this.abortControllers.set(file.uid, abortController)
        
        const CHUNK_SIZE = uploadChannel === 'discord' ? 9 * 1024 * 1024 : 16 * 1024 * 1024
        const totalChunks = Math.ceil(file.size / CHUNK_SIZE)
        
        let precomputedSha256 = null
        if (uploadChannel === 'huggingface') {
            try { precomputedSha256 = await this.computeSha256(file) } catch(e){}
        }

        try {
            const initFormData = new FormData()
            initFormData.append('originalFileName', file.name)
            initFormData.append('originalFileType', file.type)
            initFormData.append('totalChunks', totalChunks)
            
            const initRes = await axios({
                 url: '/upload' + `?serverCompress=${fileItem.serverCompress}&uploadChannel=${uploadChannel}` +
                     (this.channelName ? `&channelName=${encodeURIComponent(this.channelName)}` : '') +
                     `&uploadNameType=${this.uploadNameType}&autoRetry=${this.autoRetry}&uploadFolder=${this.uploadFolder}&initChunked=true`,
                 method: 'post', data: initFormData, withAuthCode: true
            })
            
            if (!initRes.data.success) throw new Error(initRes.data.message)
            const uploadId = initRes.data.uploadId
            fileItem.totalChunks = totalChunks
            fileItem.uploadId = uploadId
            
            const chunkProgress = new Array(totalChunks).fill(0)
            let hasError = false
            let isCancelled = false
            
            // Sequential or concurrent upload logic? Original used concurrency pool of 3.
            // I'll implement a simple pool.
            const poolLimit = 3
            const chunks = Array.from({length: totalChunks}, (_, i) => i)
            const activePool = new Set()
            
            const uploadChunk = async (chunkIndex) => {
                if (hasError || isCancelled) return
                const start = chunkIndex * CHUNK_SIZE
                const end = Math.min(start + CHUNK_SIZE, file.size)
                const chunk = file.slice(start, end)
                
                const fd = new FormData()
                fd.append('file', chunk, `${file.name}.part${String(chunkIndex).padStart(3,'0')}`)
                fd.append('chunkIndex', chunkIndex)
                fd.append('totalChunks', totalChunks)
                fd.append('uploadId', uploadId)
                fd.append('originalFileName', file.name)
                
                let retries = 3
                while(retries > 0) {
                     if (hasError || isCancelled) return
                     try {
                         await axios({
                             url: '/upload' + `?serverCompress=${fileItem.serverCompress}&uploadChannel=${uploadChannel}` +
                                  (this.channelName ? `&channelName=${encodeURIComponent(this.channelName)}` : '') +
                                  `&uploadNameType=${this.uploadNameType}&autoRetry=${this.autoRetry}&uploadFolder=${this.uploadFolder}&chunked=true`,
                             method: 'post', data: fd, withAuthCode: true, signal: abortController.signal,
                             onUploadProgress: (evt) => {
                                 chunkProgress[chunkIndex] = Math.round((evt.loaded / evt.total) * 100)
                                 const totalP = Math.round(chunkProgress.reduce((a,b)=>a+b,0) / totalChunks)
                                 this.handleProgress({ percent: totalP, file })
                             }
                         })
                         return
                     } catch(e) {
                         if (e.name === 'CanceledError') { isCancelled = true; return }
                         retries--
                         if (retries === 0) throw e
                         await new Promise(r => setTimeout(r, 1000))
                     }
                }
            }
            
            // Execute pool
            while (chunks.length > 0 && !hasError && !isCancelled) {
               while(activePool.size < poolLimit && chunks.length > 0) {
                   const p = uploadChunk(chunks.shift()).then(() => activePool.delete(p)).catch(e => {
                       hasError = true
                       activePool.delete(p)
                   })
                   activePool.add(p)
               }
               await Promise.race(activePool)
            }
            await Promise.all(activePool)
            
            if (isCancelled) return
            if (hasError) throw new Error('Chunk upload failed')
            
            // Merge
             const mergeFormData = new FormData()
             mergeFormData.append('uploadId', uploadId)
             mergeFormData.append('totalChunks', totalChunks)
             mergeFormData.append('originalFileName', file.name)
             if (precomputedSha256) mergeFormData.append('sha256', precomputedSha256)
             
             const mergeRes = await axios({
                 url: '/upload' + `?serverCompress=${fileItem.serverCompress}&uploadChannel=${uploadChannel}` +
                     (this.channelName ? `&channelName=${encodeURIComponent(this.channelName)}` : '') +
                     `&uploadNameType=${this.uploadNameType}&autoRetry=${this.autoRetry}&uploadFolder=${this.uploadFolder}&chunked=true&merge=true`,
                 method: 'post', data: mergeFormData, withAuthCode: true
             })
             this.handleSuccess(mergeRes, file)

        } catch(e) {
            if(e.name === 'CanceledError') return
             if (this.autoReUpload) {
                this.exceptionList.push({ file, retryCount: fileItem.retryCount })
            }
            this.handleError(e, file)
            // Cleanup
             if (fileItem.uploadId) this.cleanupUploadResources(fileItem.uploadId, fileItem.totalChunks)
        } finally {
            this.abortControllers.delete(file.uid)
            this.onUploadComplete()
        }
    },
    cleanupUploadResources(uploadId, totalChunks) {
         axios.get(`/upload?cleanup=true&uploadId=${uploadId}&totalChunks=${totalChunks}`)
    },
    async uploadToHuggingFaceDirect(options) {
        // Implementation from Step 85/89
        const { file } = options
        const fileItem = this.fileList.find(i => i.uid === file.uid)
        if(!fileItem) return
        
        const abortController = new AbortController()
        this.abortControllers.set(file.uid, abortController)
        
        try {
            const sha256 = await this.computeSha256(file)
            const sampleBytes = new Uint8Array(await file.slice(0, 512).arrayBuffer())
            const fileSample = btoa(String.fromCharCode(...sampleBytes))
            
            const uploadInfoRes = await axios({
                 url: '/api/huggingface/getUploadUrl',
                 method: 'post',
                 data: { fileSize: file.size, fileName: file.name, fileType: file.type, sha256, fileSample, channelName: this.channelName, uploadNameType: this.uploadNameType, uploadFolder: this.uploadFolder },
                 withAuthCode: true, signal: abortController.signal
            })
            if (!uploadInfoRes.data.success) throw new Error(uploadInfoRes.data.error)
            const uploadInfo = uploadInfoRes.data
            
            if (!uploadInfo.alreadyExists && uploadInfo.needsLfs && uploadInfo.uploadAction) {
                const { href, header } = uploadInfo.uploadAction
                if (header?.chunk_size) {
                    await this.uploadToHuggingFaceMultipart(file, uploadInfo, abortController)
                } else {
                     await fetch(href, { method: 'PUT', headers: header || {}, body: file, signal: abortController.signal })
                }
            }
            
            const commitRes = await axios({
                 url: '/api/huggingface/commitUpload',
                 method: 'post',
                 data: { fullId: uploadInfo.fullId, filePath: uploadInfo.filePath, sha256, fileSize: file.size, fileName: file.name, fileType: file.type, channelName: uploadInfo.channelName },
                 withAuthCode: true, signal: abortController.signal
            })
             if (!commitRes.data.success) throw new Error(commitRes.data.error)
             this.handleSuccess({ data: [{ src: commitRes.data.src }] }, file)
             
        } catch(e) {
             if(e.name === 'CanceledError' || e.name === 'AbortError') return
              if (this.autoReUpload) {
                this.exceptionList.push({ file, retryCount: fileItem.retryCount })
            }
             this.handleError(e, file)
        } finally {
            this.abortControllers.delete(file.uid)
            this.onUploadComplete()
        }
    },
    async uploadToHuggingFaceMultipart(file, uploadInfo, abortController) {
         // Simplified Multipart logic
         const { href, header } = uploadInfo.uploadAction
         const chunkSize = parseInt(header.chunk_size)
         const parts = Object.keys(header).filter(k => /^[0-9]+$/.test(k))
         const completeParts = []
         
         for (const part of parts) {
             if (abortController.signal.aborted) throw new Error('Canceled')
             const idx = parseInt(part) - 1
             const start = idx * chunkSize
             const end = Math.min(start + chunkSize, file.size)
             const chunk = file.slice(start, end)
             
             const res = await fetch(header[part], { method: 'PUT', body: chunk, signal: abortController.signal })
             if (!res.ok) throw new Error('Part upload failed')
             const etag = res.headers.get('ETag')
             completeParts.push({ partNumber: parseInt(part), etag })
             
              const progress = 10 + Math.round((parseInt(part) / parts.length) * 80)
              this.handleProgress({ percent: progress, file })
         }
         
         const res = await fetch(href, {
             method: 'POST',
             headers: { 'Accept': 'application/vnd.git-lfs+json', 'Content-Type': 'application/vnd.git-lfs+json' },
             body: JSON.stringify({ oid: uploadInfo.oid, parts: completeParts }),
             signal: abortController.signal
         })
         if (!res.ok) throw new Error('Multipart complete failed')
    },
    
          handleProgress(event) {
             const fileItem = this.fileList.find(item => item.uid === event.file.uid)
             if (fileItem) fileItem.progreess = event.percent
          },
          handleSuccess(response, file) {
               const fileItem = this.fileList.find(item => item.uid === file.uid)
               if (!fileItem) return
               try {
                  const uploadChannel = fileItem.uploadChannel || this.uploadChannel
                  if (uploadChannel !== 'external') {
                      const srcID = response.data[0].src.replace('/file/', '')
                      const prefix = this.useCustomUrl === 'true' ? this.customUrlPrefix : this.rootUrl
                       fileItem.url = `${window.location.protocol}//${window.location.host}/file/` + srcID
                       fileItem.finalURL = prefix + srcID
                       fileItem.mdURL = `![${file.name}](${prefix + srcID})`
                       fileItem.htmlURL = `<img src="${prefix + srcID}" alt="${file.name}" width=100% />`
                       fileItem.ubbURL = `[img]${prefix + srcID}[/img]`
                       fileItem.srcID = srcID
                  }
                  fileItem.progreess = 100
                  fileItem.status = 'success'
                  this.saveToHistory(fileItem)
                  this.$message.success(`${this.truncateFilename(file.name)} `)
                  setTimeout(() => {
                        const item = this.fileList.find(i => i.uid === file.uid)
                        if (item) item.status = 'done'
                  }, 1000)
               } catch (error) {
                   this.$message.error('')
                   fileItem.status = 'exception'
               }
          },
          handleError(err, file) {
               const fileItem = this.fileList.find(item => item.uid === file.uid)
               if (!fileItem) return
               this.$message.error(`${this.truncateFilename(file.name)} `)
               fileItem.status = 'exception'
                if (this.autoReUpload) this.scheduleAutoRetry()
          },
           beforeUpload(file) {
               return new Promise(async (resolve, reject) => {
                   let processedFile = file
                   
                   const canConvertToWebp = this.convertToWebp && file.type.includes('image') && !file.type.includes('webp') && !file.type.includes('gif') && !file.type.includes('svg')
                   if (canConvertToWebp) {
                       try {
                           const converted = await this.convertImageToWebp(file)
                           if (converted) processedFile = converted
                       } catch (e) {}
                   }

                   const needCustomCompress = processedFile.type.includes('image') && this.customerCompress && processedFile.size / 1024 / 1024 > this.compressBar
                   const isLtLim = processedFile.size / 1024 / 1024 <= 1024 || this.uploadChannel !== 'telegram'

                   const pushFileToQueue = (f, serverCompress) => {
                       const fileUrl = URL.createObjectURL(f)
                       this.fileList.push({
                           uid: f.uid,
                           name: f.name,
                           url: fileUrl,
                           finalURL: '', mdURL: '', htmlURL: '', ubbURL: '', srcID: '',
                           status: 'uploading',
                           progreess: 0,
                           serverCompress: serverCompress,
                           retryCount: 0,
                           uploadChannel: this.uploadChannel // Bind current channel
                       })
                       resolve(f)
                   }

                   if (needCustomCompress) {
                        imageConversion.compressAccurately(processedFile, 1024 * this.compressQuality).then(res => {
                            if (res.size / 1024 / 1024 > 1024 && this.uploadChannel === 'telegram') {
                                this.$message.error('')
                                reject('File too large')
                                return
                            }
                            const newFile = new File([res], processedFile.name, { type: res.type })
                            newFile.uid = file.uid
                            const myUploadCount = this.uploadCount++
                             const needServerCompress = this.serverCompress && newFile.size / 1024 / 1024 < 10 && this.uploadChannel === 'telegram'
                             if (myUploadCount === 0) pushFileToQueue(newFile, needServerCompress)
                             else setTimeout(() => { pushFileToQueue(newFile, needServerCompress); this.uploadCount-- }, 300 * myUploadCount)
                        }).catch(reject)
                   } else if (isLtLim) {
                        const myUploadCount = this.uploadCount++
                        const needServerCompress = this.uploadChannel === 'telegram' && this.serverCompress && (processedFile.type.includes('image') ? processedFile.size / 1024 / 1024 < 10 : true)
                        if (myUploadCount === 0) pushFileToQueue(processedFile, needServerCompress)
                        else setTimeout(() => { pushFileToQueue(processedFile, needServerCompress); this.uploadCount-- }, 300 * myUploadCount)
                   } else {
                       this.$message.error('')
                       reject('File too large')
                   }
               })
           },

    }
}
</script>

