<template>
    <div class="history-container" :class="{ 'active': show }">
        <div class="history-header d-flex align-center justify-space-between px-6 py-4">
            <div class="header-left d-flex align-baseline">
                <h2 class="text-h5 font-weight-bold gradient-text mr-4"></h2>
                <span class="text-caption text-medium-emphasis"> {{ totalCount }} </span>
            </div>
            <div class="header-right d-flex gap-2">
                <v-btn icon variant="elevated" density="comfortable" @click="toggleViewMode" class="glass-btn">
                     <v-tooltip activator="parent" location="bottom"></v-tooltip>
                     <v-icon>{{ viewMode === 'grid' ? 'mdi-view-list' : 'mdi-view-grid' }}</v-icon>
                </v-btn>
                <v-btn icon variant="elevated" density="comfortable" color="error" @click="clearHistory" class="glass-btn">
                     <v-tooltip activator="parent" location="bottom"></v-tooltip>
                     <v-icon>mdi-delete-sweep</v-icon>
                </v-btn>
                 <v-btn icon variant="elevated" density="comfortable" @click="$emit('close')" class="glass-btn">
                     <v-icon>mdi-close</v-icon>
                </v-btn>
            </div>
        </div>
        
        <div class="history-content flex-grow-1 overflow-y-auto px-6 py-4" v-if="historyList.length > 0" ref="historyContent" @scroll="handleScroll">
            <div v-for="group in groupedHistory" :key="group.date" class="history-group mb-6">
                <div class="timeline-header d-flex align-center mb-4 relative">
                    <div class="timeline-dot"></div>
                    <span class="text-h6 font-weight-bold gradient-text">{{ group.date }}</span>
                </div>

                <!-- Grid View -->
                <div v-if="viewMode === 'grid'" class="grid-view">
                    <v-row>
                        <v-col v-for="item in group.items" :key="item.time" cols="6" sm="4" md="3" lg="2">
                            <v-card class="grid-item fill-height" elevation="4" rounded="lg">
                                <div class="grid-preview relative">
                                    <v-img 
                                        v-if="isImage(item.name)" 
                                        :src="item.url" 
                                        cover 
                                        aspect-ratio="1"
                                        class="align-end"
                                    >
                                        <template v-slot:error>
                                            <div class="d-flex align-center justify-center fill-height bg-grey-lighten-2">
                                                <v-icon>mdi-image-broken</v-icon>
                                            </div>
                                        </template>
                                    </v-img>
                                     <video 
                                        v-else-if="isVideo(item.name)" 
                                        :src="item.url" 
                                        muted 
                                        style="width: 100%; height: 100%; object-fit: cover;"
                                    ></video>
                                    <div v-else class="d-flex align-center justify-center fill-height bg-grey-lighten-4" style="height: 160px;">
                                        <v-icon size="48" color="primary">mdi-file-document-outline</v-icon>
                                    </div>
                                    
                                    <div class="grid-overlay d-flex align-center justify-center">
                                        <div class="d-flex gap-2">
                                            <v-btn icon="mdi-content-copy" size="small" variant="flat" color="primary" @click.stop="copyLink(item.url)"></v-btn>
                                            <v-btn icon="mdi-open-in-new" size="small" variant="flat" class="glass-btn" @click.stop="openLink(item.url)"></v-btn>
                                            <v-btn icon="mdi-delete" size="small" variant="flat" color="error" @click.stop="deleteItem(item)"></v-btn>
                                        </div>
                                    </div>
                                </div>
                                <div class="pa-3">
                                    <div class="text-truncate text-subtitle-2 font-weight-medium mb-1" :title="item.name">{{ item.name }}</div>
                                    <div class="text-caption text-medium-emphasis">{{ formatTime(item.time) }}</div>
                                </div>
                            </v-card>
                        </v-col>
                    </v-row>
                </div>

                <!-- List View -->
                <div v-else class="list-view d-flex flex-column gap-3">
                     <v-card v-for="item in group.items" :key="item.time" class="list-item d-flex align-center pa-3" elevation="2" rounded="lg">
                        <v-avatar rounded="lg" size="50" class="mr-4 bg-grey-lighten-4">
                             <v-img v-if="isImage(item.name)" :src="item.url" cover></v-img>
                             <video v-else-if="isVideo(item.name)" :src="item.url" style="width: 100%; height: 100%; object-fit: cover;"></video>
                             <v-icon v-else color="primary">mdi-file-document-outline</v-icon>
                        </v-avatar>
                        
                        <div class="flex-grow-1 min-width-0 mr-4">
                            <div class="text-subtitle-2 font-weight-bold text-truncate">{{ item.name }}</div>
                            <div class="text-caption text-medium-emphasis text-truncate">{{ item.url }}</div>
                        </div>
                        
                        <div class="text-caption text-medium-emphasis mr-4 hidden-xs-only" style="min-width: 140px; text-align: right;">
                            {{ formatTime(item.time) }}
                        </div>
                        
                        <div class="d-flex gap-2">
                            <v-btn icon="mdi-content-copy" size="x-small" variant="tonal" color="primary" @click="copyLink(item.url)"></v-btn>
                            <v-btn icon="mdi-open-in-new" size="x-small" variant="tonal" @click="openLink(item.url)"></v-btn>
                            <v-btn icon="mdi-delete" size="x-small" variant="tonal" color="error" @click="deleteItem(item)"></v-btn>
                        </div>
                     </v-card>
                </div>
            </div>
            
            <!-- Load More -->
            <div v-if="hasMore" class="d-flex justify-center pa-4">
                <div v-if="loadingMore" class="d-flex align-center text-medium-emphasis">
                    <v-progress-circular indeterminate size="20" width="2" color="primary" class="mr-2"></v-progress-circular>
                    <span>...</span>
                </div>
                <div v-else class="text-caption text-medium-emphasis"></div>
            </div>
            <div v-else-if="historyList.length > 0" class="text-center pa-4 text-caption text-medium-emphasis"></div>
        </div>
        
        <div v-else class="d-flex flex-column align-center justify-center flex-grow-1 text-medium-emphasis">
            <v-icon size="64" class="mb-4" color="disabled">mdi-history</v-icon>
            <p class="text-h6"></p>
        </div>
    </div>
</template>

<script>
export default {
    name: 'UploadHistory',
    props: {
        show: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            historyList: [],
            allHistory: [],
            viewMode: 'grid',
            pageSize: 30,
            currentPage: 0,
            loadingMore: false,
            totalCount: 0,
        }
    },
    watch: {
        show(val) {
            if (val) {
                this.resetAndLoad()
            }
        }
    },
    computed: {
        hasMore() {
            return this.historyList.length < this.totalCount
        },
        groupedHistory() {
            const groups = {}
            this.historyList.forEach(item => {
                const date = new Date(item.time)
                const dateStr = `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')}`
                if (!groups[dateStr]) {
                    groups[dateStr] = []
                }
                groups[dateStr].push(item)
            })
            return Object.keys(groups).sort((a, b) => new Date(b) - new Date(a)).map(date => ({
                date,
                items: groups[date]
            }))
        }
    },
    mounted() {
        const savedMode = localStorage.getItem('historyViewMode')
        if (savedMode) {
            this.viewMode = savedMode
        }
    },
    methods: {
        resetAndLoad() {
            this.historyList = []
            this.currentPage = 0
            this.loadAllHistory()
            this.loadMore()
        },
        loadAllHistory() {
            try {
                const history = JSON.parse(localStorage.getItem('uploadHistory') || '[]')
                this.allHistory = history.sort((a, b) => b.time - a.time)
                this.totalCount = this.allHistory.length
            } catch (e) {
                console.error('Failed to load history', e)
                this.allHistory = []
                this.totalCount = 0
            }
        },
        loadMore() {
            if (this.loadingMore || !this.hasMore) return
            this.loadingMore = true
            setTimeout(() => {
                const start = this.currentPage * this.pageSize
                const end = start + this.pageSize
                const newItems = this.allHistory.slice(start, end)
                this.historyList = [...this.historyList, ...newItems]
                this.currentPage++
                this.loadingMore = false
            }, 50)
        },
        handleScroll(e) {
            const container = e.target
            const scrollTop = container.scrollTop
            const scrollHeight = container.scrollHeight
            const clientHeight = container.clientHeight
            if (scrollHeight - scrollTop - clientHeight < 100) {
                this.loadMore()
            }
        },
        toggleViewMode() {
            this.viewMode = this.viewMode === 'grid' ? 'list' : 'grid'
            localStorage.setItem('historyViewMode', this.viewMode)
        },
        clearHistory() {
             if(confirm('')) {
                this.historyList = []
                this.allHistory = []
                this.totalCount = 0
                this.currentPage = 0
                localStorage.removeItem('uploadHistory')
                this.$message.success('')
             }
        },
        deleteItem(item) {
             if(confirm('')) {
                this.historyList = this.historyList.filter(i => i.time !== item.time)
                this.allHistory = this.allHistory.filter(i => i.time !== item.time)
                this.totalCount = this.allHistory.length
                try {
                    localStorage.setItem('uploadHistory', JSON.stringify(this.allHistory))
                    this.$message.success('')
                } catch (e) {}
             }
        },
        isImage(fileName) {
            const imageExtensions = ['jpg', 'jpeg', 'png', 'gif', 'bmp', 'webp', 'svg', 'tiff', 'ico', 'avif', 'heic', 'jfif', 'pjpeg', 'pjp'];
            const extension = fileName ? fileName.split('.').pop().toLowerCase() : '';
            return imageExtensions.includes(extension);
        },
        isVideo(fileName) {
            const videoExtensions = ['mp4', 'webm', 'ogg', 'mkv'];
            const extension = fileName ? fileName.split('.').pop().toLowerCase() : '';
            return videoExtensions.includes(extension);
        },
        formatTime(timestamp) {
            const date = new Date(timestamp)
            return date.toLocaleString()
        },
        copyLink(url) {
            navigator.clipboard.writeText(url).then(() => {
                this.$message.success('')
            }).catch(() => {
                this.$message.error('')
            })
        },
        openLink(url) {
            window.open(url, '_blank')
        }
    }
}
</script>

<style scoped>
